import { date } from './constants.js';

const transformWeekDayToString = () => {
  const weekDay = date.getDay();

  let weekDayString;

  switch (weekDay) {
    case 0: {
      return (weekDayString = '');
    }
    
    case 1: {
      return (weekDayString = '');
    }

    case 2: {
      return (weekDayString = '');
    }

    case 3: {
      return (weekDayString = '');
    }

    case 4: {
      return (weekDayString = '');
    }

    case 5: {
      return (weekDayString = '');
    }

    case 6: {
      return (weekDayString = '');
    }
  }
};

export default transformWeekDayToString;